.technology {
  margin-bottom: 156px;
  &__card {
    @include pageGrid;
    margin-bottom: 116px;
    @include onTablet {
      margin-bottom: 141px;
      grid-template-rows: auto auto 1fr;
    }
    @include onDesktop {
      margin-bottom: 170px;
    }

  }
  &__title {
    margin-bottom: 30px;
    @include onTablet {
      grid-column: 5 / 9;
    }
    @include onDesktop {
      grid-column: 7 / 13;
    }
    &--revers {
      @include onTablet {
        grid-column: 1 / 1;
      }
      @include onDesktop {
        grid-column: 1 / 7;
      }
    }
  }
  &__subtitle {
    color: $color-light-gray;
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 30px;
    @include onTablet {
      grid-column: 5 / 9;
      font-size: 16px;
      line-height: 24px;
    }
    @include onDesktop {
      grid-column: 7 / 13;
    }
    &--revers {
      @include onTablet {
        grid-column: 1 / 5;
        font-size: 16px;
        line-height: 24px;
      }
      @include onDesktop {
        grid-column: 1 / 7;
      }
    }
  }
  &__images {
    margin-bottom: 20px;
    @include hover(transform, scale(1.02));
    @include onTablet {
      height: 100%;
      grid-column: 1 / 5;
      grid-row: 1/4;
    }
    @include onDesktop {
      grid-column: 1 / 7;
      width: 582px;
    }
    &--revers {
      @include onTablet {
        height: 100%;
        grid-column: 5 / 9;
        grid-row: 1/4;
      }
      @include onDesktop {
        grid-column: 7 / 13;
        width: 582px;
      }
    }
  }
  &__image {
    height: 100%;
    width: 100%;
  }

  &__button {
    height: 20px;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    color: $color-aqua;
    text-decoration: none;
    &::after {
      border-bottom: solid 1px $color-aqua;
      bottom: 0;
      content: "";
      display: block;
      transition: all 0.3s;
      -webkit-transition: all 0.3s;
      width: 0;
    }
    &:hover::after {
      width: 50%;
    }
    @include onTablet {
      grid-column: 5 / 7;
    }
    @include onTablet {
      grid-column: 5 / 9;
    }
    @include onDesktop {
      grid-column: 7 / 10;
    }
    &--revers {
      height: 20px;
      font-weight: 700;
      font-size: 12px;
      line-height: 14px;
      text-transform: uppercase;
      color: #0db2b3;
      text-decoration: none;

      &::after {
        border-bottom: solid 1px $color-aqua;
        bottom: 0;
        content: "";
        display: block;
        transition: all 0.3s;
        -webkit-transition: all 0.3s;
        width: 0;
      }
      &:hover::after {
        width: 50%;
      }
    }

  }
}
